/**
 * @param {string} url - 
 * @param {object} params - 
 * @return {Promise} -promise
 * @throws {Error} - 
 */
export function navigate(url, params = {}) {
  try {
    url += (url?.indexOf("?") < 0 ? "?" : "&") + param(params);

    return new Promise((resovle, reject) => {
      uni.navigateTo({
        url,
        success: (e) => {
          resovle(e);
        },
        fail: (e) => {
          reject(e);
        },
      });
    });
  } catch (e) {
    throw new Error("");
  }
}

