/**
 * 
 * @param {string} title - 
 * @param {string} content - 
 * @param {function} confirm -  true 
 * @param {function} cancel -  true 
 * @param {boolean} showCancel -  true
 * @param {string} cancelText - ""
 * @param {string} cancelColor - "#000000"
 * @param {string} confirmText - ""
 * @param {string} confirmColor - 
 */
export function showModal(
  title,
  content,
  confirm,
  cancel,
  showCancel = true,
  cancelText = "",
  cancelColor = "#000000",
  confirmText = "",
  confirmColor = "#007aff"
) {
  uni.showModal({
    title,
    content,
    showCancel,
    cancelText,
    cancelColor,
    confirmText,
    confirmColor,
    success: function (res) {
      if (res.confirm) {
        console.log("");
        confirm();
      } else if (res.cancel) {
        console.log("");
        cancel();
      }
    },
  });
}

