import axios from "axios";
import qs from "qs";
import Vue from "vue";
import settle from "axios/lib/core/settle";
import buildURL from "axios/lib/helpers/buildURL";
import { toast } from "../utils/index";
import { STATUSCODES } from "../constant";
import ENV_CONFIG from '../config/env'
import axiosRetry from 'axios-retry';

const service = axios.create({
  withCredentials: true,
  crossDomain: true,
  baseURL: baseURL,
  timeout: 6000,
});

let baseURL = ENV_CONFIG[process.env.VUE_APP_ENV_TYPE].baseUrl;


//  Axios Retry
axiosRetry(service, {
  retries: 3, // 
  retryCondition: (error) => {
    // 
    return (
      axiosRetry.isNetworkOrIdempotentRequestError(error) ||
      STATUSCODES.includes(error.response.status)
    );
  },
  retryDelay: (retryCount) => {
    // 
    return retryCount * 1000;
  },
});

service.interceptors.request.use(
  (config) => {
    try {
      const { isRetry = false } = config;
      if (!isRetry) {
        axiosRetry(service, { retries: 0 }); // 
      }

      const token = uni.getStorageSync("token");
      if (token) {
        // tokenhttp headertoken
        config.headers.authorization = token; //Authorization
      }
      config.headers["Content-Type"] =
        config.method.toLocaleLowerCase() === "post"
          ? "application/x-www-form-urlencoded"
          : "application/json";
      return config;
    } catch (e) {
      console.log("", e);
      //   uni.hideNavigationBarLoading();
      //   uni.hideLoading();
      toast("");
    }
  },
  (err) => {
    // uni.hideNavigationBarLoading();
    // uni.hideLoading();
    return Promise.reject(err);
  }
);
// http response 
